  display: none;
}

.show {
  display: block;
}

.abs {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.elip {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.touch-tap {
  position: relative;
}

.touch-tap:before {
  position: absolute;
  content: '';
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transform: scale(2);
}


